body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  line-height: 1.3;
}
html,
body,
#root {
  height: 100%;
}

a {
  text-decoration: none;
}

.light {
  color: #2c2d30;
  background: #fff;
}

.light a:link,
.light a:visited {
  color: #0576b9;
}

.light a {
  color: #086898;
}

.light a:active,
.light a:focus,
.light a:hover {
  color: #005e99;
}

.dark {
  color: #ffffffe0;
  background: #282c35;
}

.dark a:link,
.dark a:visited {
  color: #e88030;
}

.dark a {
  color: #ff9c52;
}

.dark a:active,
.dark a:focus,
.dark a:hover {
  color: #e68339;
}

.App {
  height: 100%;
  box-sizing: border-box;
  padding-top: 2rem;
}

.App-container {
  width: 300px;
  margin: auto;
}

.App-header {
  margin-bottom: 1rem;
  position: relative;
}

.App-theme-switcher {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 3;
}

dt {
  color: #747475;
  margin-bottom: 0.2rem;
  font-size: 0.8rem;
}

dd {
  margin: 0;
}

dd:after {
  content: '';
  display: block;
  height: 1px;
  background: #ddd;
  margin: 0.8rem 0.2rem;
}

.dark dd:after {
  background: #505050;
}

.ProfileImage {
  width: 300px;
  height: 300px;
}

.ProfileImage-caption {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: #ffffff;
  font-size: 1.6rem;
}

.dark .ProfileImage img {
  -webkit-filter: brightness(0.75);
          filter: brightness(0.75);
}

.ThemeSwitcher-button {
  border: 0;
  background: transparent;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  font-size: 26px;
  text-align: center;
  cursor: pointer;
  padding: 0;
}

.ThemeSwitcher-button:focus {
  outline-color: orangered;
}

